<template>
	<div class="scene-headline">
		<h1 v-if="headline">{{ $t(headline) }}</h1>
		<div class="scene-headline__manchet" v-if="manchet">
			{{ $t(manchet) }}
		</div>
	</div>
</template>

<script>
export default {
	name: 'SceneHeadline',
	props: {
		headline: {
			type: [Boolean, String], // false for Branded-video component without title
		},
		manchet: {
			type: [Boolean, String],
			default: false,
		},
	},
};
</script>

<style lang="scss" scoped>
@import '../assets/global';

.scene-headline {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 2;
	max-width: rem(900);

	font-family: $font-stack;

	&__manchet {
		margin-top: rem(12);
		font-size: rem($manchet-min);

		@include mq($from: screen-md){
			font-size: rem($manchet-mid);
		}
		@include mq($from: screen-lg){
			font-size: rem($manchet-max);
		}
	}
}
h1 {
	font-size: rem($display-1-min);
	font-family: $font-stack;
	font-weight: normal;
	text-align: center;

	// @include mq($from: screen-sm){
	// 	font-size: rem(42);
	// }
	@include mq($from: screen-md){
		font-size: rem($display-1-mid);
	}
	@include mq($from: screen-lg){
		font-size: rem($display-1-max);
	}
}
</style>



