<template>
	<div class="scene-content" :class="[className, inView ? 'active-pointer' : '']">

		<LadybirdSmallAnimation
			ref="ladybird1"
			id="ladybird1"
			position="ladybird1"
			width=100
			height=100
			v-on:animationCompleted="ladybirdAnimationCompleted"
			:sequenceDelay="1900"
		/>
		<LadybirdSmallAnimation
			ref="ladybird2"
			id="ladybird2"
			position="ladybird2"
			width=100
			height=100
			:sequenceDelay="2800"
		/>
		<!-- <LadybirdSmallAnimation
			ref="ladybird3"
			id="ladybird3"
			position="ladybird3"
			width=80
			height=80
			:sequenceDelay="1700"
		/> -->
		
		<!-- ONELEAF is out for now <FlowerOneLeaf
			ref="FlowerOneLeaf"
			id="flower-oneleaf"
			position="flower-oneleaf--top-left"
			width=400
			height=500
			/> -->
		<FlowerTwoLeaves
			ref="FlowerTwoLeaves"
			id="flower-twoleaves"
			position="flower-twoleaves--top-right"
			width=400
			height=500
			/>

		<div class="feed-wrapper" ref="feed">
			<InstagramFeed :feed="instagramFeed" :token="instagramToken" />
		</div>

		<div class="content" ref="content">
			<div class="ladybird-fixed">
				<img src="//cdn.sorthvid.design/images/ladybird/ladybird.png" alt="">
			</div>
			<div class="text">
				{{$t('ladybird_content_headline')}}
				<div class="skinny">{{$t('pandoragarden_hashtag')}}</div>
			</div>
		</div>
	</div>
</template>

<script>
import {MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import InstagramFeed from '../InstagramFeed';
import LadybirdSmallAnimation from '../animations/LadybirdSmall';
import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

export default {
	name: 'SceneContentLadybird',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		InstagramFeed,
		LadybirdSmallAnimation,
		FlowerTwoLeaves,
	},
	props: {
		options: Object,
		className: String,
	},
	// mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		// this.$nextTick(this.setupScene);
	// },
	data() {
		return {
			instagramFeed: this.$store.state.instagram.feed,
			instagramToken: this.$store.state.instagram.token,
		};
	},
	methods: {
		ladybirdAnimationCompleted() {
			// console.log('ladybird animation completed');
		},
		instagramFeedLoaded() {
			this.$nextTick(this.setupScene);
		},
		setupScene(){
			var bg = document.querySelector('.bg--main'),
				content = this.$refs.content,
				feed = this.$refs.feed;

			var feedHeight = feed.clientHeight;

			var tlProgress = 0;
			var scrollDirection = 'down';
			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentTlProgress = introTl.progress();
				if(currentTlProgress > tlProgress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				tlProgress = introTl.progress();
			}});

			this.introTl
				.set(bg, {autoAlpha: 0});

			this.introTl
				.add('intro-start')
				.fromTo(bg, 1,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('content-start')
				.addLabel('ladybird1-intro', 'content-start-=0.3') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-intro', 'content-start-=0.1')
				.addLabel('twoleaves-intro', 'content-start-=0.1')
				// .addLabel('ladybird3-intro', 'content-start-=0.1')
				.fromTo(content, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 0,
						delay: -1,
					},
					{
						y: '50%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.fromTo(feed, .4,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight,
						autoAlpha: 1,
						delay: -0.3,
					},
					{
						y: this.$store.state.windowHeight / 25,
						autoAlpha: 1,
						ease: Linear.easeNone,
						delay: -0.3,
					}
				)
				.fromTo(feed, 2,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: this.$store.state.windowHeight / 25,
						autoAlpha: 1,
						// delay: -1,
					},
					{
						y: -feedHeight,
						autoAlpha: 1,
						ease: Linear.easeNone,
						// delay: -1,
					}
				)
				.add('content-outro')
				.addLabel('ladybird1-outro', 'content-outro-=0.6') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-outro', 'content-outro-=0.4')
				.addLabel('twoleaves-outro', 'content-outro-=0.4')
				// .addLabel('ladybird3-outro', 'content-outro-=0.2')
				.fromTo(content, 1,
					{
						// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
						y: '50%',
						autoAlpha: 1,
						delay: -0.4,
					},
					{
						y: '-=' + (this.$store.state.windowHeight + (this.$store.state.windowHeight / 100 * 30)) + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -0.4,
					}
				)
				.add('content-end')
				.fromTo(bg, 0.6,
					{
						autoAlpha: 1,
						delay: -1,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1,
					}
				)
				.add('end')
				.progress(1)
				.progress(0)
				// .pause()
				;

			var ladybirdAnimations = [
				{
					el: this.$refs.ladybird1,
					introLabel: 'ladybird1-intro',
					outroLabel: 'ladybird1-outro',
				},
				{
					el: this.$refs.ladybird2,
					introLabel: 'ladybird2-intro',
					outroLabel: 'ladybird2-outro',
				},
				{
					el: this.$refs.FlowerTwoLeaves,
					introLabel: 'twoleaves-intro',
					outroLabel: 'twoleaves-outro',
				},
				// {
				// 	el: this.$refs.ladybird3,
				// 	introLabel: 'ladybird3-intro',
				// 	outroLabel: 'ladybird3-outro',
				// },
			];

			ladybirdAnimations.forEach(function(ladybird, i){
				// trigger ladybird intro animations based on gsap introTl timeline labels
				this.introTl.addCallback(() => {
					// console.log('ladybird this: ', this);
					if(scrollDirection == 'down'){
						ladybird.el.setForward();
						ladybird.el.play();
					} else {
						ladybird.el.playOutro();
					}
				}, ladybird.introLabel);

				// // trigger ladybird outro animations based on gsap introTl timeline labels
				this.introTl.addCallback(() => {
					// console.log('ladybird this: ', this);
					if(scrollDirection == 'down'){
						ladybird.el.playOutro();
					} else {
						ladybird.el.setForward();
						ladybird.el.play();
					}
				}, ladybird.outroLabel);
			}.bind(this));
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	opacity: 1;

	.scene-headline { // h2
		position: relative;
		font-size: rem(60);
		top: 4vw;
		left: 50%;
		transform: translateX(-50%);
		text-align: center;
	}

	.content {
		position: absolute;
		top: 0;
		right: rem(100);
		opacity: 0;
		visibility: hidden;
		width: 50vw;
		z-index: 10;

		.text {
			width: 60%;
			font-size: rem(80);
			font-weight: bold;
			z-index: 2;
		}
		.skinny {
			font-weight: normal;
		}

		.ladybird-fixed { // the ladybird image
			position: relative;
			float: right;
			width: rem(300);
			height: rem(600);

			z-index: 1;
			overflow: hidden;

			img {
				position: absolute;
				top: 30%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 280%;
			}
		}
	}
	.ladybird1,
	.ladybird2,
	.ladybird3 {
		z-index: 0;
		position: absolute;
	}
	.ladybird1 {
		transform: rotate(20deg);
		top: 24%;
		left: rem(30);

		width: rem(200); // width is, for some reason, double the height
		height: rem(100);
	}
	.ladybird2 {
		transform: rotate(10deg);
		top: 18%;
		left: 80%;

		width: rem(200);
		height: rem(100);
	}
	// .ladybird3 {
	// 	transform: rotate(-15deg);
	// 	top: 77%;
	// 	left: 70%;

	// 	width: rem(320);
	// 	height: rem(160);
	// }

	.feed-wrapper {
		position: absolute;
		top: 0;
		left: 10vw;
		opacity: 0;
		visibility: hidden;
		width: rem(720);
		padding-top: rem(50);
	}
}
</style>



