<script>
import {MapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneContent',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	// data() {
	//   return {
	//   };
	// },
	methods: {
		setupScene(){
			console.log('footer setting up');

			const bg = document.querySelector('.bg--main');
			const content = this.$refs.content;

			this.introTl
				.set(bg, { autoAlpha: 0 });

			this.introTl
				.add('intro-start')
				.to(bg, 1, {
					autoAlpha: 1,
					ease: Linear.easeNone,
				})
				.add('content-start')
				.fromTo(content, 1,
					{
						y: '30px',
						autoAlpha: 0,
						// ease: Power4.easeIn
					},
					{
						y: 0,
						autoAlpha: 1,
						ease: Power4.easeIn
					},
					'-=0.5'
				)
				.add('content-end')
				.add('end')
				.progress(1)
				.progress(0)
				;
		},
	},
};
</script>

