		margin: 0 auto;

		opacity: 0;
		// visibility: visible;
		visibility: hidden;
	}
	h2 {
		margin-bottom: rem(100);
		text-align: center;
		font-size: rem(80);
		max-width: rem(600);
		margin: 0 auto rem(100);

		opacity: 1;
		visibility: visible;
		// visibility: hidden;
	}

	.button {
		max-width: rem(450);
		margin: auto;
	}
}
