<template>
	<div class="modal-overlay" @click="closeOpenModal" ref="overlay" :class="[hasAnimated ? 'animated' : '']">
		<div class="modal" @click.stop ref="modal" :class="[hasLoaded ? 'loaded' : '']">
			<div class="modal-closeButton" @click="closeOpenModal"></div>
			<picture v-if="modal.isCard" class="card-modal-image">
				<source media="(max-width: 566px)" :srcset="modal.content.images.cardSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.cardLarge">
				<img :src="modal.content.images.cardLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard">
				<source media="(max-width: 566px)" :srcset="modal.content.images.lifestyleSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.lifestyleLarge">
				<img :src="modal.content.images.lifestyleLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard">
				<img :src="modal.content.images.packshot" @load="loaded">
			</picture>
			<div class="modal-content">
				<div class="modal-price" v-if="!modal.isCard">{{ $t(`${modal.content.sku}_price`) }}</div>
				<a :href="modal.content.link" class="modal-link button">{{ $t('general_shop_now') }}</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex';
	import { TweenMax, Power4 } from 'gsap';

	export default {
		name: 'Modal',
		computed: mapState([
			'modal',
		]),
		data(){
			return {
				numberLoaded: 0,
				startCoords: {},
				hasLoaded: false,
				hasAnimated: false,
			};
		},
		mounted() {
			this.position = this.$store.state.position;
			this.startCoords = this.modal.coords;
		},
		methods: {
			...mapActions([
				'closeOpenModal',
			]),
			openModal() {
				TweenMax.set('.modal', {
					height: 'auto',
					left: '50%',
					top: '50%',
					width: '90%',
					xPercent: -50,
					yPercent: -50,
					opacity: 1,
				});
				TweenMax.from('.modal', 0.6, {
					position: 'absolute',
					left: this.modal.coords.left,
					top: this.modal.coords.top,
					width: this.modal.coords.width,
					height: this.modal.coords.height,
					xPercent: 0,
					yPercent: 0,
					ease: Power4.easeOUT,
					onComplete: () => {
						this.hasAnimated = true;
					},
				});
			},
			openMobile() {
				TweenMax.set('.modal', {
					height: '100%',
					left: '50%',
					top: '50%',
					width: '100%',
					xPercent: -50,
					yPercent: -50,
					opacity: 1,
				});
				TweenMax.from('.modal', 0.4, {
					position: 'absolute',
					left: this.modal.coords.left,
					top: this.modal.coords.top,
					width: this.modal.coords.width,
					height: this.modal.coords.height,
					xPercent: 0,
					yPercent: 0,
					ease: Power4.easeOUT,
					onComplete: () => {
						this.hasAnimated = true;
					},
				});
			},
			loaded() {
				this.numberLoaded += 1;
				if (this.modal.isCard || this.numberLoaded > 1) {
					this.hasLoaded = true;
					window.innerWidth >= 567 ? this.openModal() : this.openMobile();
				}
			},
		},
	};
</script>

<style lang="scss" scoped>
	@import '../assets/global';

	.modal-overlay {
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgba(0, 0, 0, 0);
		z-index: 9999;
		transition: all 1s ease;

		&.animated {
			background: rgba(0, 0, 0, .4);
		}
	}

	.modal {
		background: white;
		width: 90%;
		height: auto;
		max-width: rem(1500);
		display: flex;
		align-items: stretch;
		position: absolute;
		top: 50%;
		left: 50%;
		opacity: 0;

		picture {
			flex: 0 0 50%;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-fit: cover;
			}
			&.card-modal-image {
				flex: 0 0 100%;
				display: block;
			}
		}

		@include mq($until: screen-xs) {
		    flex-wrap: wrap;
			align-items: flex-start;
			align-content: flex-start;

			picture {
				flex: 0 0 100%;
				height: 40vh;

				&:nth-of-type(2) {
					height: 40vh;
					img {
						object-fit: contain;
					}
				}

				&.card-modal-image {
					height: 100%;
					display: block;
				}
			}
		}
	}

	.modal-closeButton {
		cursor: pointer;
		height: rem(40);
		width: rem(40);
		background: white;
		position: absolute;
		top: rem(30);
		right: rem(30);
		border-radius: 50%;
		box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);
		opacity: 0;
		transition: opacity 1s ease;

		.animated & {
			opacity: 1;
		}

		&:before, &:after {
			display: block;
			content: '';
			height: rem(2);
			width: rem(25);
			background: black;
			position: absolute;
			left: 50%;
			top: 50%;
			border-radius: rem(2);
			transform: translate(-50%) rotate(45deg);
		}

		&:after {
			transform: translate(-50%) rotate(-45deg);
		}

		&:hover {
			&:before {
				animation: closeHoverRight .5s ease;
			}

			&:after {
				animation: closeHoverLeft .5s ease;
			}
		}

		@keyframes closeHoverRight {
			0% {
				transform: translate(-50%) rotate(45deg);
			}
			50% {
				transform: translate(-50%) rotate(30deg);
			}
			100% {
				transform: translate(-50%) rotate(45deg);
			}
		}
		@keyframes closeHoverLeft {
			0% {
				transform: translate(-50%) rotate(-45deg);
			}
			50% {
				transform: translate(-50%) rotate(-30deg);
			}
			100% {
				transform: translate(-50%) rotate(-45deg);
			}
		}

	}

	.modal-content {
		box-sizing: border-box;
		position: absolute;
		right: 0;
		bottom: 0;
		padding: rem(30);
		opacity: 0;
		transition: opacity 1s ease;

		.animated & {
			opacity: 1;
		}

		@include mq($until: screen-xs) {
		    width: 100%;
		}
	}

	.modal-price {
		color: $color-nevada;
		font-size: rem(30);
		font-family: $font-stack;
		letter-spacing: rem(2);
		text-align: right;
		margin-bottom: rem(20);

		@include mq($until: screen-xs) {
			text-align: center;
			font-size: rem(20);
			margin-bottom: rem(15);
		}
	}
</style>



