<template>
	<div class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<h2 class="scene-headline" ref="headline">{{ $t('butterfly_content_headline') }}</h2>

		<ButterflyAnimation
			ref="butterfly1"
			id="butterfly1"
			position="butterfly1"
			width=500
			height=500
			:sequenceDelay="3000"
		/>
		<!-- ROEN: Just an example usage of animationCompleted callback <ButterflyAnimation
			ref="butterfly1"
			v-on:animationCompleted="butterflyAnimationCompleted"
		/> -->
		<ButterflyAnimation
			ref="butterfly2"
			id="butterfly2"
			position="butterfly2"
			width=250
			height=250
			:sequenceDelay="2800"
		/>
		<ButterflyAnimation
			ref="butterfly3"
			id="butterfly3"
			position="butterfly3"
			width=300
			height=300
			:sequenceDelay="1800"
		/>
		<FlowerTwoLeaves
			ref="FlowerTwoLeaves"
			id="flower-twoleaves"
			position="flower-twoleaves--bottom-right"
			width=400
			height=500
			/>
		<div class="content" ref="content">
			<div class="butterfly-card" v-for="card in cards" @click="openClosedModal({sku: card.sku, isCard: true, target: $event.target})">
				<div class="butterfly-card__image-wrapper" v-if="card.images.cardThumb">
					<img :src="card.images.cardThumb" alt="Butterfly image 1">
				</div>
				<div class="text" v-if="card.cardText">
					{{ $t( card.cardText ) }}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex';
	import ProgressMixin from '../mixins/ProgressMixin';

	import { ONGOING } from '../../constants';

	import SceneHeadline from '../SceneHeadline';
	import ButterflyAnimation from '../animations/Butterfly';
	import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

	export default {
		name: 'SceneContentButterfly',
		mixins: [ProgressMixin],
		components: {
			SceneHeadline,
			ButterflyAnimation,
			FlowerTwoLeaves,
		},
		props: {
			options: Object,
			className: String,
			content: Object,
		},
		mounted() {
			// console.log('mounted - this.options.name: ', this.options.name);
			this.contentWidth = this.$refs.content.clientWidth;
			this.windowWidth = window.innerWidth;
			// console.log('contentWidth: ', this.contentWidth);
			this.$nextTick(this.setupScene);
		},
		data() {
			return {
				// contentWidth,
				// windowWidth,
				// inView: this.$store.scenes.filter(store => store.name === this.props.options.name)[0].state,
			};
		},
		computed: mapState({
			cards: state => state.products.filter(product => product.type === 'butterfly'),
		}),
		methods: {
			...mapActions([
				'openClosedModal',
			]),
			butterflyAnimationCompleted() {
				console.log('butterfly animation complete');
			},
			// runPrepare(prepareProgress){
			// 	if(this.scenePrepareRan === false && prepareProgress == true){
			// 		// console.log('now preparing - scene: ', this.options.name);
			// 		this.scenePrepared = true;
			// 		this.scenePrepareRan = true;
			// 	}
			// },
			// runPrepareReverse(prepareProgress){
			// 	if(this.scenePrepareReverseRan === false && prepareProgress == true){
			// 		// console.log('now preparing - scene: ', this.options.name);
			// 		this.sceneReversePrepared = true;
			// 		this.scenePrepareReverseRan = true;
			// 	}
			// },

			setupScene() {
				const bg = document.querySelector('.bg--main');
				const headline = this.$refs.headline;
				const butterfly1 = this.$refs.butterfly1;
				const content = this.$refs.content;

				// this.introTl
				// 	.set(content, {autoAlpha: 0});

				// console.log('content scene: ', this.options.name);

				var tlProgress = 0;
				var scrollDirection = 'down';
				this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
					var currentTlProgress = introTl.progress();
					if(currentTlProgress > tlProgress){
						scrollDirection = 'down'
					} else {
						scrollDirection = 'up'
					}
					tlProgress = introTl.progress();
				}});

				this.introTl
					.set(bg, { autoAlpha: 0 });

				this.introTl
					.add('intro-start')
					.to(bg, 1, {
						autoAlpha: 1,
						ease: Linear.easeNone,
					})
					.add('content-start')
					.addLabel('butterfly1-intro', 'content-start+=0.3') // timing cannot be 0, as it doesn't provoke the direction
					.addLabel('butterfly2-intro', 'content-start-=0.1')
					.addLabel('butterfly3-intro', 'content-start-=0.3')
					.addLabel('twoleaves-intro', 'content-start-=0.3')
					.fromTo(content, 5,
						{
							// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
							// y: '50%',
							x: `${this.windowWidth}px`,
							autoAlpha: 1,
							// ease: Linear.easeNone,
						},
						{
							// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
							// y: '50%',

							// x: '-' + this.contentWidth - 1800 + 'px',
							x: `-${this.contentWidth}px`,
							autoAlpha: 1,
							ease: Linear.easeNone,
						},
						'intro-start',
					)
					.fromTo(headline, .5,
						{
							autoAlpha: 0,
							ease: Linear.easeNone,
						},
						{
							autoAlpha: 1,
							ease: Linear.easeNone,
						},
						'intro-start+=0.6',
					)
					.add('outro')
					.addLabel('butterfly1-outro', 'outro-=0.3') // timing cannot be 0, as it doesn't provoke the direction
					.addLabel('butterfly2-outro', 'outro-=0.5')
					.addLabel('butterfly3-outro', 'outro-=0.4')
					.addLabel('twoleaves-outro', 'outro-=0.4')
					.to(bg, 1, {
						autoAlpha: 0,
						delay: -0.7,
						ease: Linear.easeNone,
					})
					.to(headline, 0.5,
						{
							autoAlpha: 0,
							delay: -1.2,
							ease: Linear.easeNone,
						},
					)
					.add('end')
					.progress(1)
					.progress(0)
					// .pause()
					;

				var butterflyAnimations = [
					{
						el: this.$refs.butterfly1,
						introLabel: 'butterfly1-intro',
						outroLabel: 'butterfly1-outro',
					},
					{
						el: this.$refs.butterfly2,
						introLabel: 'butterfly2-intro',
						outroLabel: 'butterfly2-outro',
					},
					{
						el: this.$refs.butterfly3,
						introLabel: 'butterfly3-intro',
						outroLabel: 'butterfly3-outro',
					},
					{
						el: this.$refs.FlowerTwoLeaves,
						introLabel: 'twoleaves-intro',
						outroLabel: 'twoleaves-outro',
					},
				];

				butterflyAnimations.forEach(function(butterfly, i){
					// trigger butterfly intro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('butterfly this: ', this);
						if(scrollDirection == 'down'){
							butterfly.el.setForward();
							butterfly.el.play();
						} else {
							butterfly.el.playOutro();
						}
					}, butterfly.introLabel);

					// // trigger butterfly outro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('butterfly this: ', this);
						if(scrollDirection == 'down'){
							butterfly.el.playOutro();
						} else {
							butterfly.el.setForward();
							butterfly.el.play();
						}
					}, butterfly.outroLabel);
				}.bind(this));
			},
		},
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/global';

	.scene--butterfly-content {
		opacity: 1;
		// background-color: red;

		.scene-headline { // h2
			position: relative;
			font-size: rem(60);
			top: calc(18vh - rem(30));
			left: 50%;
			transform: translateX(-50%);
			text-align: center;
			max-width: rem(600);

			@include mq($until: screen-xs) {
				font-size: rem(24);
				top: rem(60);
				padding: 0 rem(30);
			}
		}

		.content {
			position: absolute;
			bottom: 0;
			left: 0;
			transform: translateX(-105%);
			display: flex;
			padding: rem(100) 0 0;
			flex-wrap: nowrap;
			align-items: center;
			height: 100%;
			z-index: 2;
			box-sizing: border-box;

			@include mq($from: screen-md) {
				padding: calc(rem(80) + rem(40) + 9vh) 0 rem(30);
				box-sizing: border-box;
				top: 0;
			}
		}

		.butterfly1,
		.butterfly2,
		.butterfly3 {
			z-index: 2;
			position: absolute;
		}
		.butterfly1 {
			transform: rotate(20deg);
			top: 14%;
			left: 5%;

			width: rem(250);
			height: rem(250);
		}
		.butterfly2 {
			transform: rotate(25deg);
			top: 80%;
			left: 40%;

			width: rem(150);
			height: rem(150);
		}
		.butterfly3 {
			transform: rotate(-15deg);
			top: 77%;
			left: 55%;

			width: rem(160);
			height: rem(160);
		}

		.butterfly-card {
			min-width: rem(270);
			width: 20vw;
			margin: 0 rem(20);
			box-shadow: rem(20) rem(20) rem(50) rgba(0, 0, 0, .1);
			cursor: pointer;
			background: $color-white;

			&__image-wrapper {
				width: 100%;
				overflow: hidden;

				img {
					will-change: transform;
					transition: transform .5s ease-in-out;
					display: block;
					width: 100%;
				}
			}

			.text {
				display: block;
				// width: 100%;
				color: #222;
				padding: rem(20);
			}

			&:hover {
				.butterfly-card__image-wrapper {
					img {
						transform: scale(1.1);
					}
				}
			}
		}
	}
</style>



