<template>
	<div class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<div class="bg bg--clover" ref="bg"></div>

		<CloverAnimation
			ref="clover1"
			id="clover1"
			position="flower-clover--top-right"
			width=500
			height=500
			v-on:animationCompleted="cloverAnimationCompleted"
		/>

		<picture class="clover__poster-image" ref="poster">
    		<source srcset="//cdn.sorthvid.design/images/clover/poster-small.png" media="(max-width: 1024px)">
    		<source srcset="//cdn.sorthvid.design/images/clover/poster.png" media="(min-width: 1025px)">
    		<img src="//cdn.sorthvid.design/images/clover/Ladybird_bg_model.png" />
		</picture>


		<!-- <h2 class="scene-headline" ref="headline">Clover content headline</h2> -->
		<div class="content" ref="content">
			<span class="clover-headline" ref="headline">{{$t('clover_content_headline')}}</span>


			<a href="#dsa" class="bubble bubble--1 bubble--product" ref="b1" @click="openClosedModal({sku: '197949', target: $event.target})">
				<img src="//cdn.sorthvid.design/images/products/PNGTRPNT_197949_RGB.png" alt="" />
			</a>
			<a href="#dsa" class="bubble bubble--2" ref="b2">
				<img src="//cdn.sorthvid.design/images/clover/shutterstock_591683126_DESAT_CROP.jpg" alt="" />
			</a>
			<a href="#dsa" class="bubble bubble--3" ref="b3">
				<img src="//cdn.sorthvid.design/images/clover/SPR19_BTL_MODEL_GARDEN_02_RGB_CROP.jpg" alt="" />
			</a>
			<a href="#dsa" class="bubble bubble--4 bubble--product" ref="b4" @click="openClosedModal({sku: '397965NAG', target: $event.target})">
				<img src="//cdn.sorthvid.design/images/products/PNGTRPNT_397965NAG_RGB.png" alt="" />
			</a>
		</div>
	</div>
</template>

<script>
import {mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
// import { TimelineMax } from 'gsap'; // TimelineMax gets included via ProgressMixin
import CloverAnimation from '../animations/Clover';

export default {
	name: 'SceneContentClover',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		CloverAnimation,
	},
	props: {
		options: Object,
		className: String,
	},
	data() {
		return {
			products: [
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
			]
		};
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	// data() {
	//   return {
	//   };
	// },
	methods: {
		...mapActions([
			'openClosedModal',
		]),
		cloverAnimationCompleted(){
			console.log('clover animation completed');
		},
		setupScene(){
			const bgMain = document.querySelector('.bg--main');
			const bg = this.$refs.bg;
			const content = this.$refs.content;
			const poster = this.$refs.poster;
			const headlineTl = new TimelineMax({ paused: true });

			var staggerElements = [
				this.$refs.headline,
				this.$refs.b1, 
				this.$refs.b2, 
				this.$refs.b3, 
				this.$refs.b4, 
			];

			var tlProgress = 0;
			var scrollDirection = 'down';
			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentTlProgress = introTl.progress();
				if(currentTlProgress > tlProgress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				tlProgress = introTl.progress();
			}});

			this.introTl
				.set(content, {autoAlpha: 1})
				.set(bg, {autoAlpha: 0})
				.set(bgMain, {autoAlpha: 0});

			this.introTl
				.add('start')
				.fromTo(bg, 2,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.to(poster, 1, 
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('contentStart')
				.staggerFrom(staggerElements, 2,
					{
						autoAlpha: 0,
						y: '+=' + this.$store.state.windowHeight / 1.2 + 'px',
						ease: Power4.easeInOut,
						// delay: -2, // delay per item
					},
					'0.25', // stagger item delay
					'contentStart-=1.5' // start from 1.5 "dur" before label
				)
				.add('content-outro')
				.staggerTo(staggerElements, 2,
					{
						y: `-=${ this.$store.state.windowHeight }px`,
						// top: '-144px', // ROEN HOW DO I REMOVE THEM FREM THE SCENE
						ease: Power4.easeIn,
						// delay: -2,
					},
					'-0.25' // making the order in the outro reverse-ish
				)

				.add('content-end')
				.to(poster, 1, {
					autoAlpha: 0,
						ease: Linear.easeNone,
				})
				.fromTo(bg, 2,
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					}
				)
				
				.add('end')
				.progress(1)
				.progress(0)
				;

			// trigger clover intro animations based on gsap introTl timeline labels
			this.introTl.addCallback(() => {
				if(scrollDirection == 'down'){
					this.$refs.clover1.setForward();
					this.$refs.clover1.play();
				} else {
					this.$refs.clover1.playOutro();
				}
			}, 'contentStart+=0.2');

			// // trigger clover outro animations based on gsap introTl timeline labels
			this.introTl.addCallback(() => {
				if(scrollDirection == 'down'){
					this.$refs.clover1.playOutro();
				} else {
					this.$refs.clover1.setForward();
					this.$refs.clover1.play();
				}
			}, 'content-end-=0.5');

		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/global';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	opacity: 1;

	.clover-headline { // h2
		position: absolute;
		top: 13%;
		left: 50%;
		width: 45%;
		max-width: rem(450);
		// transform: translateX(-50%);
		z-index: 10;

		font-family: $font-stack-bold;
		font-size: rem($title-1-min);
		text-align: left;

		@include mq($from: screen-sm){
			font-size: rem($title-1-mid);
			top: 36%;
			left: 59%;
		}
		@include mq($from: screen-md){
			font-size: rem($title-1-max);
		}
		@include mq($from: screen-lg){
			left: rem(800);
		}
	}

	.content {
		opacity: 0;
	}

	.clover {
		&-bubble {
			position: absolute;
			// right: 12%;
			// top: 20%;
			// font-size: rem(80);
			max-width: rem(400);
			font-weight: bold;
			z-index: 8;
		}
		&__poster-image {
			position: absolute;
			top: rem($main-menu-height);
			left: 0;
			opacity: 0;
			z-index: 2;

			@include mq($from: screen-md){
				top: rem($main-menu-height-md);
			}

			> img {
				max-height: calc(100vh - 30px);
			}
		}
	}
	
	.bubble { //
		position: absolute;
		top: 40%;
		// left: 40%;
		
		text-decoration: none;
	
		width: rem(150);
		
		border-radius: 50%;
		text-align: center;
		z-index: 3;

		overflow: hidden;
		@include mq($from: screen-sm){
			width: rem(234);
		}
		&::before {
			content: '';
			padding-top: 100%;
			height: 0;
			display: block;
		}
		>img {
			transition: transform .5s;
			position: absolute;
			top: 0;
			left: 0;
			max-width: 100%;
			&:hover {
				transform: scale(1.1);
			}
		}
		&--product {
			background: white;
			overflow: inherit;
			z-index: 5;
		}
		&--1 { // product
			left: 38%;
			// transform: translate3d(0%, 0%, 0);
			>img {
				top: -20%;
				left: -17%;
				width: 130%;
			}
			@include mq($from: screen-sm){
				left: 28%;
				width: rem(205);
			}
			@include mq($from: screen-lg){
				left: rem(520);
			}
		}
		&--2 { //leaves
			transform: translate3d(0%, 50%, 0); // y-position
			width: rem(217);
			left: 22%;
			@include mq($from: screen-sm){
				width: rem(337);
				transform: translate3d(0%, 0%, 0); // y-position
				left: 55%;
			}
			@include mq($from: screen-lg){
				left: rem(775);
			}
			@include mq($from: screen-xl){
				//width: rem(488);
			}
			
		
		}
		&--3 { // cowboys
			left: 60%;
			transform: translate3d(0%, -50%, 0);
			width: rem(217);
			@include mq($from: screen-sm){
				width: rem(337);
				left: 44%;
			}
			@include mq($from: screen-lg){
				left: rem(600);
			}
			@include mq($from: screen-xl){
				//width: rem(488);
			}
		}
		&--4 { // prduct two
			transform: translate3d(0%, 0%, 0);
			width: rem(117);
			left: 63%;
			top: 70%;
			>img {
				top: -20%;
				position: absolute;
			
			}
			@include mq($from: screen-sm){
				left: 82%;
				top: 65%;
				width: rem(193);
			}
			@include mq($from: screen-lg){
				left: rem(1000);
				
			}
		}
	}
}
</style>



